import React, {useState} from "react";

const Home = () => {
  let navigation = document.querySelector("#home nav .menus");
  const [name, setName] = useState("");

  const shrink = () => {
    if (window.scrollY >= 80) {
      setName("scroll-home");
    } else {
      setName("");
    }
  };

  window.addEventListener("scroll", shrink);

  const toggleNav = () => {
    if (navigation.style.display === "block") {
      navigation.style.display = "none";
    } else {
      navigation.style.display = "block";
    }
  };

  return (
    <section id="home" className={name}>
      <nav>
      <div className="nav-header container">
      <a href="#home" className="logo">Mohammad Rahi</a>
        <i onClick={toggleNav} className="fas fa-bars"></i>
    </div>
     <div className="menus">
        <a onClick={toggleNav} href="#about">About</a>
        <a onClick={toggleNav} href="#skills">Skills</a>
        <a onClick={toggleNav} href="#portfolio">Portfolio</a>
        <a onClick={toggleNav} href="#contact">Contact</a>
    </div>
    </nav>
    <div className="profession">
      <p>
Frontend developer
      </p>
    </div>
    <a href="#about" className="bottomTrigger">
      <i className="fas fa-arrow-down"></i>
    </a>
    </section>
  );
}

export default Home;